import styles from './index.less'
import { Modal } from 'antd'
import { connect, useDispatch } from 'umi'
import type { WalletStateType, WalletType } from '@/models/dataType'
import { walletList } from '@/common/config'

type Fn = () => void

function LoginPanel(props: { visible: boolean; closeModal: Fn } & WalletStateType) {
  const { visible, closeModal, dataSourceChainId } = props
  const list = () => {
    let _list = [] as WalletType[]
    const keys = Object.keys(walletList)
    keys.forEach((v) => {
      if (walletList[v].chainId === dataSourceChainId) {
        _list.push(walletList[v])
      }
    })
    return _list
  }
  const dispatch = useDispatch()
  const connectWallet = async (name: string) => {
    closeModal()
    await dispatch({
      type: 'wallet/connectWallet',
      payload: {
        name,
      },
    })
  }

  return (
    <Modal
      width={304}
      closable={false}
      open={visible}
      getContainer={() => document.getElementById('J_Page') || document.body}
      footer={null}
      wrapClassName={styles.loginPanel}
      onCancel={closeModal}
    >
      {list().map((wallet: WalletType) => (
        <div className={styles.item} onClick={() => connectWallet(wallet.name)} key={wallet.label}>
          <div className={styles.icon}>
            <img src={wallet.icon} className={styles.icon} />
          </div>
          <div className={styles.text}>{wallet.label}</div>
        </div>
      ))}
    </Modal>
  )
}

const mapStateToProps = ({ wallet }: { wallet: WalletStateType }) => {
  return {
    ...wallet,
  }
}

export default connect(mapStateToProps)(LoginPanel)
