import styles from './chain.less'
import { connect, useDispatch } from 'umi'
import type { WalletStateType } from '@/models/dataType'
// import Btn from '@/components/btn'
import { getChainById } from './chainData'
import menuData from './chainData'
import { Dropdown } from 'antd'
import { DownloadArr } from '@/components/ui'
import type { chainDataType } from './chainData'
import { useState } from 'react'

function Chain(props: WalletStateType) {
  const dispatch = useDispatch()
  const { dataSourceChainId } = props

  const getCurrentIndex = () => {
    let _index = menuData.findIndex((v) => v.id === dataSourceChainId)
    if (_index < 0) _index = 0
    return _index
  }

  // console.log('dataSourceChainId', dataSourceChainId)
  const { symbol, icon } = getChainById(dataSourceChainId)
  const [currentIndex, setCurrentIndex] = useState(getCurrentIndex())

  const changeChain = async (item: chainDataType, index: number) => {
    if (item.id !== dataSourceChainId) {
      await dispatch({
        type: 'wallet/disconnectWallet',
      })
    }
    await dispatch({
      type: 'wallet/changeChainId',
      payload: {
        chainItem: item,
      },
    })
    setCurrentIndex(index)
  }

  const menu = (
    <div className={styles.panel}>
      {menuData.map((item, index) => {
        return (
          <div className={`${styles.wrap} ${styles.item_wrap}`} key={item.key}>
            <div
              className={
                index === currentIndex ? `${styles.item} ${styles.item_selected}` : styles.item
              }
              onClick={async () => changeChain(item, index)}
            >
              <img src={item.icon}   className={styles.icon}/> {item.label}
            </div>
          </div>
        )
      })}
    </div>
  )

  return (
    <Dropdown
      overlay={menu}
      getPopupContainer={() => document.getElementById('J_Page') || document.body}
      pointAtCenter={true}
      placement="bottom"
      trigger={['hover', 'click']}
    >
      <div className={styles.chain}>
        <div className={styles.wrap}>
          <div className={styles.box}>
            <img src={icon} style={{ width: 24, height: 24 }} />
            <div className={styles.name}>{symbol}</div>
            <DownloadArr />
          </div>
        </div>
      </div>
    </Dropdown>
  )
}

const mapStateToProps = ({ wallet }: { wallet: WalletStateType }) => {
  return {
    ...wallet,
  }
}

export default connect(mapStateToProps)(Chain)
