import styles from './menu.less'
import _ from '@/i18n'
import { history } from 'umi'
import { CaretDownOutlined } from '@ant-design/icons'
import { Dropdown } from 'antd'
// import { EarnMenu, PoolMenu } from './submenu'
import { agentVersion } from '@/common/utils'
import Icon from '@/components/icon'

const agent = agentVersion()
const isPhone = agent === 'iPhone' || agent === 'Android'

export default function Menu() {
  const menus = [
    {
      key: 'trade',
      label: _('trade'),
      path: '/trade',
      icon: 'icon-swap',
    },
    {
      key: 'pools',
      label: _('pools'),
      path: '/pools',
      // children: <PoolMenu />,
      icon: 'icon-books',
    },
    // {
    //   key: 'liquidate',
    //   label: _('liquidate'),
    //   path: '/liquidate',
    // },
    {
      key: 'earn',
      label: _('earn'),
      // children: <EarnMenu />,
      path: '/earn',
      icon: 'icon-circles',
    },
    {
      key: 'portfolio',
      label: _('portfolio'),
      path: '/portfolio',
      icon: 'icon-block',
    },
    {
      key: 'stats',
      label: _('stats'),
      path: '',
      icon: 'icon-chart',
    },
  ]

  const openPage = (path: string) => {
    history.push(path)
  }
  const { pathname } = history.location
  return (
    <div className={styles.menus}>
      {menus.map((item) => {
        const { key, label, path, children, icon } = item
        const active = path ? pathname.indexOf(path) > -1 : false
        if (children && !isPhone) {
          return (
            <Dropdown
              key={key}
              overlay={children}
              getPopupContainer={() => document.getElementById('J_Page') || document.body}
            >
              <div
                className={active ? `${styles.item} ${styles.item_current}` : styles.item}
                onClick={() => path && openPage(path)}
              >
                {label}
                {children && <CaretDownOutlined />}
              </div>
            </Dropdown>
          )
        }
        return (
          <div
            className={active ? `${styles.item} ${styles.item_current}` : styles.item}
            key={key}
            onClick={() => path && openPage(path)}
          >
            {icon && <Icon type={icon} className={styles.icon} />}
            {label}
          </div>
        )
      })}
    </div>
  )
}
