import type { CommonStateType } from '@/models/dataType'
import { connect, useDispatch } from 'umi'
import { InputNumber } from 'antd'
import styles from './slip.less'
import { useState } from 'react'

function Slip(props) {
  const { slippage } = props
  const dispath = useDispatch()
  // è¿™é‡Œæ²¡å¿…è¦ï¼Œæ—¢ç„¶æ•°æ®çŠ¶æ€å·²ç»æå‡äº†ï¼Œä¹Ÿæ²¡æœ‰å¼‚æ­¥æ“ä½œï¼Œå¯ä»¥ç›´æŽ¥ä½¿ç”¨propsæ•°æ®
  // const [slip, setSlip] = useState(slippage)
  const changeSlippage = (value: number) => {
    // setSlip(value)
    dispath({
      type: 'common/changeSlippage',
      payload: {
        slippage: value,
      },
    })
  }
  return (
    <div className={styles.slip}>
      <InputNumber
        onChange={changeSlippage}
        value={slippage}
        formatter={(value) => `${value} %`}
        className={styles.slip_input}
        min={0}
      />
    </div>
  )
}

const mapStateToProps = ({ common }: { common: CommonStateType }) => {
  return common
}

export default connect(mapStateToProps)(Slip)
