import { Spin } from 'antd'
import { Loading3QuartersOutlined } from '@ant-design/icons'

export function Loading(props) {
  const { children, spinning, size = 'large' } = props
  return (
    <Spin
      spinning={spinning}
      size={size}
      indicator={
        <Loading3QuartersOutlined spin style={{ fontSize: size === 'large' ? 30 : 'auto' }} />
      }
    >
      {children}
    </Spin>
  )
}
