import _ from '@/i18n'
import styles from './index.less'
import { Dropdown } from 'antd'
import { DownloadArr } from '@/components/ui'
import { strAbbreviation } from '@/common/utils'
import Btn from '@/components/btn'
import { connect, useDispatch } from 'umi'
import type { TokenDataType, WalletStateType } from '@/models/dataType'
import MyWallet from './mywallet'

function ConnectWallet(props) {
  const { account, walletChainId, dataSourceChainId, isLogin } = props
  const wrongChain = isLogin && parseInt(walletChainId) !== parseInt(dataSourceChainId, 16)
  const dispatch = useDispatch()

  const switchNetwork = async () => {
    await dispatch({
      type: 'wallet/switchChain',
    })
  }

  if (wrongChain) {
    return (
      <div className={styles.connect_wallet}>
        <div className={styles.wrong_wrap} onClick={switchNetwork}>
          <div className={styles.wrong}>{_('switch_network')}</div>
        </div>
      </div>
    )
  }

  if (account) {
    return (
      <Dropdown
        overlay={<MyWallet />}
        getPopupContainer={() => document.getElementById('J_Page') || document.body}
        placement="bottomRight"
      >
        <div className={styles.connect_wallet}>
          <Btn>
            <div className={styles.account}>{strAbbreviation(account, [5, 5])}</div> <DownloadArr />
          </Btn>
        </div>
      </Dropdown>
    )
  }

  return (
    <div className={styles.connect_wallet}>
      <Btn className={styles.btn} height={33}>
        <div className={styles.account}>{_('connect_wallet')}</div>
      </Btn>
    </div>
  )
}

const mapStateToProps = ({
  wallet,
  tokens,
}: {
  wallet: WalletStateType
  tokens: TokenDataType
}) => {
  return {
    ...wallet,
    ...tokens,
  }
}

export default connect(mapStateToProps)(ConnectWallet)
