import { BtnSimple } from '@/components/btn'
import { DropDownArr } from '@/components/ui'
import _ from '@/i18n'
import type { WalletStateType } from '@/models/dataType'
import { connect } from 'umi'
import { message } from 'antd'
import { agentVersion } from '@/common/utils'

const agent = agentVersion()
const isPhone = agent === 'iPhone' || agent === 'Android'

function TestToken(props: WalletStateType) {
  const { account, currentWallet } = props
  const fetch = async () => {
    const res = await currentWallet.getFaucet()
    if (res.account && res.hash) {
      message.success(`Test tokens sent your address ${res.account}, txid is ${res.hash}`)
    } else if (res.error) {
      message.error(res.error)
    }
  }
  if (!account || isPhone) {
    return null
  }
  return (
    <div style={{ marginRight: 10 }}>
      <BtnSimple className="btn" style={{ fontSize: 14, fontWeight: '400' }} onClick={fetch}>
        <DropDownArr />
        <div style={{ fontSize: 14, marginLeft: 5 }}>{_('test_tokens')}</div>
      </BtnSimple>
    </div>
  )
}

const mapStateToProps = ({ wallet }: { wallet: WalletStateType }) => {
  return {
    ...wallet,
  }
}

export default connect(mapStateToProps)(TestToken)
