import styles from './index.less'
import CopySvg from '@/assets/svg/copy.svg'
import UnlinkSvg from '@/assets/svg/unlink.svg'
import Clipboard from '@/components/clipboard'
import { strAbbreviation, usdPrice } from '@/common/utils'
import { connect, useDispatch } from 'umi'
import type { TokenDataType, WalletStateType } from '@/models/dataType'
import DefultIcon from '@/components/icon/defult'
import FormatNumber from '@/components/formatNumber'
import { getChainById } from '../chainData'

function MyWallet(props) {
  const dispatch = useDispatch()
  const { account, tokenDatas = [], osdTokenData, walletChainId } = props
  const chainData = getChainById(walletChainId)

  const disconnect = () => {
    dispatch({
      type: 'wallet/disconnectWallet',
      payload: {},
    })
  }

  const list = tokenDatas.length > 0 ? [...tokenDatas, osdTokenData] : []

  return (
    <div className={`${styles.panel} ${styles.my_wallet}`}>
      <div className={styles.wallet}>
        <div className={styles.copy}>
          <div className={styles.address_wrap}>
            <a
              href={`${chainData.exporerAccountUrl}/${account}?network=${chainData.network}`}
              target="_blank"
              className={styles.address}
              rel="noreferrer"
            >
              {strAbbreviation(account, [8, 8])}
            </a>
            <Clipboard text={account}>
              <img src={CopySvg} style={{ cursor: 'pointer' }} />
            </Clipboard>
          </div>
        </div>
        <div className={styles.disconnect} onClick={disconnect}>
          <img src={UnlinkSvg} style={{ cursor: 'pointer' }} />
        </div>
      </div>
      <div className={styles.assets}>
        {list.map((item) => {
          const { symbol, icon, userTokenBalance, tokenId, userOraclePrice } = item
          const usd = usdPrice([{ token: item, amount: userTokenBalance }])
          return (
            <div className={styles.assets_item} key={tokenId}>
              <div className={styles.info}>
                <div className={styles.icon}>
                  {icon ? <img src={icon} /> : <DefultIcon tokenId={tokenId} />}
                </div>
                <div className={styles.name}>
                  <div className={styles.symbol}>{symbol}</div>
                  <div className={styles.price}>
                    <FormatNumber value={userOraclePrice} prefix="$" />
                  </div>
                </div>
              </div>
              <div className={styles.balance}>
                <div>
                  {userTokenBalance && (
                    <FormatNumber value={userTokenBalance} useAbbr={true} round={4} />
                  )}
                </div>
                <div className={styles.usd}>
                  <FormatNumber value={usd} useAbbr={true} prefix="$" />
                </div>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

const mapStateToProps = ({
  wallet,
  tokens,
}: {
  wallet: WalletStateType
  tokens: TokenDataType
}) => {
  return {
    ...wallet,
    ...tokens,
  }
}

export default connect(mapStateToProps)(MyWallet)
