import './iconfont'
import './index.less'

export default function Icon(props: {
  type: string
  style?: React.CSSProperties
  className?: string
}) {
  const { type, style, className } = props
  return (
    <svg className={`icon ${className}`} aria-hidden="true" style={style}>
      <use xlinkHref={`#${type}`} />
    </svg>
  )
}
