import styles from './index.less'
import _ from '@/i18n'
import { useEffect, useState } from 'react'
import LoginPanel from '@/layout/header/connectWallet/loginPanel'
import { connect } from 'umi'
import type { WalletStateType } from '@/models/dataType'
import { Loading } from '../loading'

function Btn(props) {
  const { isLogin } = props
  const {
    className,
    children,
    onClick,
    height,
    align,
    style,
    conditions = [],
    connecting,
    redCond,
  } = props
  const [visible, setVisible] = useState(false)
  const [cond, setCond] = useState('')

  const login = () => {
    setVisible(true)
  }

  useEffect(() => {
    let isUnmount = false
    let _cond = ''
    try {
      conditions.forEach((item) => {
        if (item.handle) {
          _cond = item.txt
          throw new Error()
        }
      })
      _cond = ''
    } catch (error) {}
    if (!isUnmount) setCond(_cond)
    return () => (isUnmount = true)
  }, [conditions])

  const evt = !isLogin ? login : !cond ? onClick : undefined
  const child = !isLogin ? _('connect_wallet') : !cond ? children : cond

  return (
    <>
      <div
        className={
          !cond && redCond
            ? `${styles.wrap} ${className} ${styles.wrong}`
            : `${styles.wrap} ${className}`
        }
      >
        <div
          className={`${styles.box} box`}
          onClick={evt}
          style={{ lineHeight: `${height - 12}px`, justifyContent: align, ...style, minWidth: 65 }}
        >
          {connecting ? <Loading size="small" /> : child}
        </div>
      </div>
      {visible && <LoginPanel visible={visible} closeModal={() => setVisible(false)} />}
    </>
  )
}

const mapStateToProps = ({ wallet, loading }: { wallet: WalletStateType; loading: any }) => {
  const { effects } = loading
  return {
    ...wallet,
    connecting: effects['wallet/connectWallet'] || false,
  }
}

export default connect(mapStateToProps)(Btn)

export function BtnSimple(props) {
  const { className, children, onClick, height, align, style } = props
  return (
    <div
      className={`${styles.box} ${className}`}
      onClick={onClick}
      style={{ lineHeight: height, justifyContent: align, ...style }}
    >
      {children}
    </div>
  )
}
