// import useDarkMode from '@/useData/useDarkMode'
import React from 'react';
import type { CommonStateType } from '@/models/dataType'
import { connect, withRouter } from 'umi'
import Footer from './footer'
import Header from './header'
import styles from './index.less'
import { ConfigProvider } from 'antd'
import enUS from 'antd/es/locale/en_US'
import QueueAnim from 'rc-queue-anim';


const ACTION_MAP = {
  PUSH: 'forward',
  POP: 'back',
  REPLACE: 'forward',
};

const ANIMATION_MAP = {
  forward: ['right', 'left'],
  back: ['left', 'right'],
};

function Layout(props: { children: any; pageClassName: any; isDarkMode: any; history: any; location: any; }) {
  const { children, pageClassName, isDarkMode, history, location } = props;
  const { pathname } = location;
  const pathArray = pathname.split('.')[0].split('/');
  const path = pathArray[pathArray.length - 1];
  const key = path || 'index';
  const { action } = history
  return (
    <div className={isDarkMode ? 'darkMode body' : 'lightMode body'} id="J_Page">

      <div className={`${styles.root} ${pageClassName}`}>

        <Header />
        <ConfigProvider locale={enUS}>
          <QueueAnim
            type={ANIMATION_MAP[ACTION_MAP[action]]}
            ease="easeInOutQuart"
            delay={0}
            interval={200}
            className={styles.content}
          >
            <div
              key={key}
              className="layout-anim-child"
            >

              {React.Children.map(children, (child) => {
                return (
                  <>
                    {React.cloneElement(child)}
                  </>
                );
              })}
              {/* <div className={styles.content}>{children}</div> */}
            </div>
          </QueueAnim>
        </ConfigProvider>
        <Footer />

      </div>

    </div >
  )
}

const mapStateToProps = ({ common }: { common: CommonStateType }) => {
  return common
}

export default withRouter(connect(mapStateToProps)(Layout))
